body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #e5e5e5;
  padding: 0;
  margin: 0; }

*, a, button {
  outline: none;
  text-decoration: none; }

img, svg {
  max-width: 100%; }

.sg-logo {
  position: fixed;
  top: 50px;
  left: 50px;
  z-index: 10; }

.sg-section {
  position: absolute;
  width: 108%;
  height: 108%;
  left: -4%;
  bottom: -4%; }
  .sg-section__bg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%; }
  .sg-section__trees {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%; }
  .sg-section[data-section="1"] {
    z-index: 4;
    bottom: -5%; }
  .sg-section[data-section="2"] {
    z-index: 3; }
  .sg-section[data-section="3"] {
    z-index: 2; }
  .sg-section[data-section="4"] {
    z-index: 1; }

.sg-sky {
  position: absolute;
  top: -100px;
  left: -2%;
  width: 104%; }
  .sg-sky svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; }

#sg-app {
  width: 100vw;
  height: 100vh; }

canvas {
  z-index: 100;
  position: relative; }

.sg-globe {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1; }
  .sg-globe #canvas_globe {
    position: absolute;
    border: 1px solid red;
    top: 0;
    left: 0;
    border-radius: 50%;
    z-index: 0;
    width: 100%;
    height: 100%; }

.sg-plane {
  position: fixed;
  top: 7%;
  left: 0;
  z-index: 1;
  width: 100%; }
  .sg-plane__wrapper {
    position: absolute;
    width: 19vw;
    animation: planeMove 25s;
    animation-iteration-count: infinite;
    animation-timing-function: linear; }

#planeSvg {
  width: 100%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: alternate; }

/* The animation code */
@keyframes planeMove {
  0% {
    right: 100%; }
  10% {
    right: 87%; }
  20% {
    right: 74%; }
  30% {
    right: 61%; }
  40% {
    right: 48%; }
  50% {
    right: 35%; }
  60% {
    right: 22%; }
  70% {
    right: 9%; }
  80% {
    right: -4%; }
  90% {
    right: -17%; }
  100% {
    right: -30%; } }

.sg-carousel {
  position: fixed;
  bottom: 10vh;
  left: 20%;
  width: 60%;
  height: 40vh;
  z-index: 5; }
  .sg-carousel__title {
    font-family: Sunrise International Demo;
    line-height: normal;
    font-size: 48px;
    color: #484d6d;
    text-transform: uppercase;
    text-align: center;
    margin-top: -200px; }
  .sg-carousel__slides {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%; }
    .sg-carousel__slides img {
      width: 300px; }
  .sg-carousel__slide {
    width: 33.3333%;
    position: absolute;
    top: 0;
    text-align: center;
    transition: all .3s; }
    .sg-carousel__slide img {
      margin: 0 auto; }
    .sg-carousel__slide[data-current="1"] {
      left: 33.3333%; }
    .sg-carousel__slide[data-current="2"] {
      left: 0;
      transform: scale(0.7); }
    .sg-carousel__slide[data-current="3"] {
      left: 66.6666%;
      transform: scale(0.7); }
  .sg-carousel__dots {
    position: absolute;
    top: 100%;
    z-index: 5;
    width: 100%;
    text-align: center; }
    .sg-carousel__dots button {
      background-color: transparent;
      display: inline-block;
      vertical-align: middle;
      margin: 0 3px;
      border: none;
      box-shadow: none;
      font-size: 0;
      width: 10px;
      height: 10px;
      background-image: url(src/img/carousel-dot.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: all .3s; }
      .sg-carousel__dots button.is-active {
        width: 20px;
        height: 20px; }
  .sg-carousel__arrow {
    position: absolute;
    bottom: 50px;
    height: 50px;
    width: 25px;
    font-size: 0;
    background-color: transparent;
    box-shadow: none;
    border: none; }
    .sg-carousel__arrow:after {
      content: '';
      position: absolute;
      width: 30px;
      height: 30px;
      border-top: 4px solid #242638;
      border-left: 4px solid #242638; }
    .sg-carousel__arrow:before {
      content: '';
      position: absolute;
      height: 40vh;
      bottom: -50px;
      width: 18vw; }
    .sg-carousel__arrow--prev {
      right: 100%; }
      .sg-carousel__arrow--prev:before {
        left: 100%; }
      .sg-carousel__arrow--prev:after {
        transform: rotate(-45deg);
        left: 5px;
        top: 5px; }
    .sg-carousel__arrow--next {
      left: 100%; }
      .sg-carousel__arrow--next:before {
        right: 100%; }
      .sg-carousel__arrow--next:after {
        transform: rotate(135deg);
        left: -15px;
        top: 5px; }
